@import url(https://fonts.googleapis.com/css2?family=Dosis:wght@400;700&display=swap);
.App {
	text-align: center;
	min-height: 60vh;
	display: flex;
	flex-direction: column;
	align-items: center;
}

#timer {
	font-family: "Dosis", "Roboto";
	color: #202020;
}

h1 {
	margin: 1rem;
	font-family: "Dosis", "Roboto";
	font-weight: bold;
	color: #383838;
}

#timer > p {
	margin: 1rem;
	padding: 0 1rem;
	font-size: large;
}

#timer > span {
	margin: 0rem;
	margin-top: 1rem;
	color: #707070;
}

#timer > img {
	margin: 4rem;
	height: 200px;
}

.form-page {
	display: flex;
	width: 100%;
	padding: 0 2rem;
}

@media screen and (max-width: 768px) {
 .form-page {
	 flex-direction: column;
 }
}

#form {
	padding: 1rem;
	width: 100%;
	font-family: "Dosis", "Roboto";
	color: #202020;
	display: flex;
	flex-direction: column;
	align-items: center;
}

#form > p {
	font-family: "Dosis", "Roboto";
	color: #4b4b4b90;
	font-size: 12px;
}

#form > * {
	margin: 1rem 0;
}

#form > .card {
	display: flex;
	flex-direction: column;
	border-radius: 0.5rem;
	padding: 1rem;
	width: 100%;
}

#form > .card > .divider {
	width: 100%;
	height: 2px;
	background-color: #bebebe90;
	margin: 1rem 0;
}

#form > .divider {
	width: 100%;
	margin: 0.5rem 0;
	border: 4px dashed #bebebe90;
}

#form > .card > input,
#form > .card > textarea,
#form > .card > button {
	font-family: "Dosis", "Roboto";
	color: #202020;
}

#form > .card > input,
#form > .card > textarea {
	border: 0;
	border-radius: 0.5rem;
	width: 100%;
}

#form > .card > input:focus,
#form > .card > textarea:focus {
	outline: none;
	box-shadow: none;
}

#form > .card > textarea {
	min-height: 20rem;
}

#form * button,
#form > label {
	text-decoration: none;
	text-transform: uppercase;
	font-family: "Dosis", "Roboto";
	font-weight: bold;
	font-size: 14px;
	border: 2px solid #707070;
	color: #707070;
	background-color: white;
	border-radius: 100px;
	outline: none;
	cursor: pointer;

	padding: 6px 24px;
	margin: 0 0.5rem;
	transition: transform 0.3s, box-shadow 0.3s;
}

#form > label {
	margin-top: 1rem;
}

@media (hover: hover) {
	#form * button:hover,
	#form > label:hover {
		box-shadow: 3px 3px 0px rgba(0, 0, 0, 0.26);
		transform: translateY(-3px) translateX(-3px);
	}
}

#form > #error-para {
	color: red;
	font-weight: bold;
	margin: 0;
	font-size: 16px;
}

.preview-text {
	font-family: "Dosis", "Roboto";
	color: #4b4b4b90;
	font-size: 14px;
	margin: 1rem 0;
}

.wrapper {
	width: 100%;
	table-layout: fixed;
}

.wrapper-inner {
	width: 100%;
}

table {
	border-spacing: 0;
	font-family: sans-serif;
	background-image: radial-gradient(white, #c2b186);
}

.outer-table {
	width: 100%;
	max-width: 670px;
	margin: 0 auto;
}

td {
	padding: 0;
}
.header {
	background-color: #c2c1c1;
	border-bottom: 3px solid #81b9c3;
}
.header p {
	text-align: center;
	padding: 1%;
	font-weight: 500;
	font-size: 11px;
	text-transform: uppercase;
}
.main-table-first {
	width: 100%;
	max-width: 610px;
	margin: 0 auto;
	background-color: #fff;
	border-radius: 6px;
	margin-top: 25px;
}
.two-column {
	text-align: center;
	font-size: 0;
	padding: 5px 0 10px 0;
}
.two-column .prev-section {
	width: 100%;
	max-width: 300px;
	display: inline-block;
	vertical-align: top;
}
.two-column .content {
	font-size: 16px;
	line-height: 20px;
	text-align: justify;
}
.content {
	width: 100%;
	padding-top: 20px;
}
.center {
	display: table;
	margin: 0 auto;
}
img {
	border: 0;
}
img.logo {
	float: left;
	margin-left: 5%;
	max-width: 200px !important;
}
#callout {
	float: right;
	margin: 4% 5% 2% 0;
	height: auto;
	overflow: hidden;
}
#callout img {
	max-width: 20px;
}
.social {
	list-style-type: none;
	margin-top: 1%;
	padding: 0;
}
.social li {
	display: inline-block;
}
.social li img {
	max-width: 15px;
	margin-bottom: 0;
	padding-bottom: 0;
}
/*--- Start Outer Table Banner Image, Text & Button --*/
.image img {
	width: 100%;
	max-width: 670px;
	height: auto;
}
.main-table {
	width: 100%;
	max-width: 610px;
	margin: 0 auto;
	background-color: #fff;
	border-radius: 6px;
}
.one-column .inner-td {
	font-size: 16px;
	line-height: 20px;
	text-align: justify;
}
.inner-td {
	padding: 10px;
}
.h2 {
	text-align: center;
	font-size: 35px;
	font-weight: 600;
	line-height: 45px;
	margin: 12px;
	color: #4a4a4a;
}
.h3 {
	text-align: left;
	font-size: 20px;
	font-weight: 500;
	line-height: 30px;
	color: #374d52;
}
.h4 {
	text-align: left;
	font-size: 18px;
	font-weight: 400;
	line-height: 20px;
	color: #374d52;
}
.h5 {
	text-align: center;
	font-size: 15px;
	font-weight: 400;
	line-height: 40px;
}
p.center {
	text-align: center;
	max-width: 580px;
	line-height: 28px;
	font-size: 20px;
}
.button-holder-center {
	text-align: center;
	margin: 5% 2% 3% 0;
}
.button-holder {
	float: right;
	margin: 5% 0 3% 0;
}
.btn {
	font-size: 15px;
	font-weight: 600;
	background: #81bac6;
	color: #fff;
	text-decoration: none;
	padding: 9px 16px;
	border-radius: 28px;
}
/*--- Start Two Column Image & Text Sections --*/
.two-column img {
	width: 100%;
	max-width: 280px;
	height: auto;
}
.two-column .text {
	padding: 10px 0;
}
/*--- Start 3 Column Image & Text Section --*/
.outer-table-2 {
	width: 100%;
	max-width: 670px;
	margin: 22px auto;
	background-color: #c2c1c1;
	border-bottom: 3px solid #81b9c3;
	border-top: 3px solid #81b9c3;
}
.three-column {
	text-align: center;
	font-size: 0;
	padding: 10px 0 30px 0;
}
.three-column .prev-section {
	width: 100%;
	max-width: 200px;
	display: inline-block;
	vertical-align: top;
}
.three-column .content {
	font-size: 16px;
	line-height: 20px;
}
.three-column img {
	width: 100%;
	max-width: 125px;
	height: auto;
}
.outer-table-2 p {
	margin-top: 6px;
	color: #fff;
	font-size: 18px;
	font-weight: 500;
	line-height: 23px;
}
/*--- Start Two Column Article Section --*/
.outer-table-3 {
	width: 100%;
	max-width: 670px;
	margin: 22px auto;
	background-color: #c2c1c1;
	border-top: 3px solid #81b9c3;
}
/*--- Start Bottom One Column Section --*/
.inner-bottom {
	padding: 22px;
}
.h1 {
	text-align: center !important;
	font-size: 25px !important;
	font-weight: 600;
	line-height: 45px;
	margin: 12px 0 20px 0;
	color: #4a4a4a;
}
.inner-bottom p {
	font-size: 16px;
	line-height: 24px;
	text-align: justify;
}
/*--- Start Footer Section --*/
.footer {
	width: 100%;
	background-color: #c2c1c1;
	margin: 0 auto;
	color: #fff;
}
.footer img {
	max-width: 135px;
	margin: 0 auto;
	display: block;
	padding: 4% 0 1% 0;
}
p.footer {
	text-align: center;
	color: #fff !important;
	line-height: 30px;
	padding-bottom: 4%;
	text-transform: uppercase;
}
/*--- Media Queries --*/
@media screen and (max-width: 400px) {
	.h1 {
		font-size: 22px;
	}
	.two-column .column,
	.three-column .column {
		max-width: 100% !important;
	}
	.two-column img {
		width: 100% !important;
	}
	.three-column img {
		max-width: 60% !important;
	}
}

@media screen and (min-width: 401px) and (max-width: 400px) {
	.two-column .column {
		max-width: 50% !important;
	}
	.three-column .column {
		max-width: 33% !important;
	}
}

@media screen and (max-width: 768px) {
	img.logo {
		float: none !important;
		margin-left: 0% !important;
		max-width: 200px !important;
	}

	#callout {
		float: none !important;
		margin: 0% 0% 0% 0;
		height: auto;
		text-align: center;
		overflow: hidden;
	}
	#callout img {
		max-width: 26px !important;
	}
	.two-column .prev-section {
		width: 100% !important;
		max-width: 100% !important;
		display: inline-block;
		vertical-align: top;
	}

	.two-column img {
		width: 100% !important;
		height: auto !important;
	}
	img.img-responsive {
		width: 100% !important;
		height: auto !important;
		max-width: 100% !important;
	}
	.content {
		width: 100%;
		padding-top: 0px !important;
	}
}

